import React from 'react';

const Card = (props) => {
    return (
         <div class="cards-style">

		       	    <div class="tutorials"> 
		       			<h2>{props.title}</h2>
		       			<p>{props.desc}</p>
		       			<button>View Details ... </button>
		       	    </div>

		       </div> 
    );
};

export default Card;